import React, { useEffect, useState } from 'react'
import request from 'superagent'

function App() {
  const [fruits, setFruits] = useState([])

  useEffect(() => {
    // https://www.fruityvice.com/api/fruit/all
    request.get('/api/fruits').then((response) => {
      const fruits = response.body
      setFruits(fruits)
    })
  }, [])

  return (
    <>
      <h1>Fruits</h1>
      <ul>
        {fruits?.map((fruit) => {
          return (
            <li key={fruit.id}>
              {fruit.name} - ({fruit.family} {fruit.genus})
            </li>
          )
        })}
      </ul>
    </>
  )
}

export default App
